import OpenAI from "openai";

// 设置 OpenAI API 密钥
const openai = new OpenAI({
  apiKey: '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', // 在此处替换为你的 OpenAI API 密钥
  dangerouslyAllowBrowser: true 
});

async function whisper(audio) {
  try {
    // 调用 OpenAI API 转录音频文件
    const transcription = await openai.audio.transcriptions.create({
      file: audio, // 替换为你的音频文件路径
      model: "whisper-1", // 使用的模型
    });

    // 输出转录结果
    // console.log(transcription.text);
    return transcription.text;
  } catch (error) {
    // 错误处理
    console.error('转录失败:', error);
    return null;
  }
}

export default whisper;
